<template>
  <div class="start" :style="{'--size': size}">
    <div class="start-bottom">
      <div class="start-top" :style="{'width': topWidth}"></div>
    </div>
  </div>
</template>
<script>

/**
 * fileName: 'ratingStar.vue'
 * createTime: 2018-07-12   14:02
 * @params: { param: mean
 * @desc: 评分星星组件
 *        图片的宽高比为5.6： 1
 */

export default {
  data () {
    return {
      ratio: 5.6
    }
  },
  props: {
    ratings: {
      type: Number,
      required: true
    },
    size: {
      type: String,
      required: true
    }
  },
  computed: {
    topWidth () {
      return parseFloat(this.size) * 5.6 * this.ratings / 5 + 'vw'
    }
  }
}
</script>
<style lang="less" scoped>

.start{
  position: relative;
  display: inline-block;
  width: calc(var(--size) * 5.6);
  height: var(--size);
  // --size: 2.666vw;
  &-bottom{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(data:image/svg+xml;base64,);
    background-size: calc(var(--size) * 5.6) var(--size);
    background-repeat: no-repeat;
  }
  &-top{
    position: absolute;
    top: 0;
    left: 0;
    // --size: 2.666vw;
    height: 100%;
    background-image: url(data:image/svg+xml;base64,);
    // background-size: cover;
    background-size: calc(var(--size) * 5.6) var(--size);
    background-repeat: no-repeat;
  }
}
</style>



